:root {
  --red-color: #891818;
  --white-color: #FFFFFF;
  --black-color: #000000;
  --gray-color:#707070;
}
*, ::before, ::after{
  box-sizing: border-box;
}
body{
  font-family: 'Roboto','Noto Sans TC';
  font-size: 24px;
  line-height: 42px;
}
h2{
  line-height: 75px;
  font-size: 64px;
}
.banner{
  color: var(--white-color);
  display: flex;
  justify-content: center;
  background: url("/assets/images/Week2/photo-1552393700-42696fb89bfa.png") center;
  background-size: cover;
  min-height: 960px;
}
.banner > div{
  align-self: center;
}
.banner .title{
  margin-bottom: 32px;
}
.banner h1{
  line-height: 94px;
  font-size: 80px;
  font-weight: 700;
}
.banner article{
  margin-bottom: 64px;
}
.banner .icon{
  display: inline-block;
  width: 60px;
  height: 60px;
  margin-right: 16px;
}

.author{
  padding: 64px 0 56px 0;
  text-align: center;
}
.author h2{
  margin-bottom: 40px;
}
.author .content{
  margin-bottom: 34px;
}
.author .content::after{
  display: block;
  content: "";
  margin: 0 auto;
  width: 384px;
  height: 1px;
  background-color: var(--black-color);
  margin-top: 32px;
}
.author .list li{
  margin-bottom: 32px;
}
.author .list li::before{
  content: "";
  display: inline-block;
  width: 22px;
  height: 22px;
  background-color: var(--red-color);
  border-radius: 50%;
  margin-right: 16px;
}

.brand{
  background-color: var(--black-color);
  color: var(--white-color);
  padding: 64px 0 160px 0;
  border: 1px solid var(--gray-color);
}
.brand .header{
  margin-bottom: 48px;
  text-align: center;
}
.brand p{
  margin-bottom: 32px;
}
.brand p.text-small{
  font-size: 20px;
}
.brand .imgWall>div{
  margin-bottom: 40px;
  text-align: center;
  display: flex;
  justify-content: center;
}
.brand .imgWall .imgItem{
  margin: 0 12px;
}
.brand .imgWall .imgItem img{
  width: 100%;
  height: auto;
}

.works{
  background-image: url("/assets/images/Week2/photo-1561291386-5be50364cfb5.png");
  background-position: bottom center;
  background-size: auto 80%;
  background-repeat: no-repeat;
  padding: 64px 0 160px 0;
}
.works .header{
  margin-bottom: 48px;
  text-align: center;
}
.works h2{
  margin-bottom: 24px;
}
.works p{
  font-size: 20px;
}
.works .imgWall>div{
  display: flex;
  justify-content: center;
  margin-bottom: 56px;
}
.works figure{
  margin: 12px;
}
.works img{
  display: block;
  margin: 0 auto;
  width: 100%;
  height: auto;
  margin-bottom: 16px;
}
.works figcaption{
  color: var(--white-color);
  font-size: 20px;
  text-align: center;
}

.footer{
  background: var(--black-color);
  padding: 64px 0 80px 0;
  color: var(--white-color);
  text-align: center;
}
.footer h2, .footer p{
  margin-bottom: 16px;
}